import { IoCubeOutline } from "react-icons/io5";
import { Container, Row, Col } from "react-bootstrap";
import React, { Component,useEffect,useState } from "react";


import coverVideo from '../../img/cover.mp4'

function Cover(props) {
    const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());
    const [useWidthScale, setUseWidthScale] = useState(false)

    function getWindowDimensions() {
        const { innerWidth: width, innerHeight: height } = window;
        return {
            width,
            height
        };
    }

    useEffect(() => {
        function handleResize() {
            setWindowDimensions(getWindowDimensions());
        }

        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    useEffect(() => {
        if (windowDimensions.height / 9 * 16 < windowDimensions.width) {
            if (!useWidthScale) setUseWidthScale(true);
        }
        else {
            if (useWidthScale) setUseWidthScale(false);
        }

    }, [windowDimensions])

    return (


        <div>    
                <div className='cover overflow-hidden'>
                    <div className={useWidthScale ? 'backVideo-width' : 'backVideo-height'}>
                        <video src={coverVideo} width='100%' height='100%' autoPlay muted loop> </video>   
                    </div>
                    <div className='mask'>
                    
                    </div>    
                                   
                </div> 
                <Container>
                    <div className='pageTitle'>
                        <p className='my-auto'><IoCubeOutline /></p>
                        <div style={{width:'2rem',height:'1rem'}} />
                        <p className='my-auto'>{props.pageName}</p>
                    </div> 
                </Container>
        </div>
    );
}

export { Cover }